@use "variables" as v;
@use "mixin" as m;

html,
body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
}

html {
  font-size: 100%;
  //   scroll-behavior: smooth;
}

body {
  font-family: "Open Sans", sans-serif;
  background: v.$white;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: v.$black;
}

li {
  list-style: none;
}

.wrapper {
  max-width: 100%;
  margin: 0 auto;
}

#mainvisual {
  width: 100vw;
  height: 100vh;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

// ==============================
// Swiper„Çπ„É©„Ç§„Éâ„Ç∑„Éß„Éº„ÅÆË®≠ÂÆö
// ==============================
.swiper-container {
	position: relative;
	width: 100vw;
	height: 100vh;
  
	.swiper-wrapper {
	  height: 100%;
	}
  
	.swiper-slide {
	  background-size: cover;
	  background-position: center center;
	  background-repeat: no-repeat;
	  height: 100vh;
	  opacity: 0; // ÂàùÊúüÁä∂ÊÖã
	  transition: opacity 1.5s ease-in-out;
  
	  &.swiper-slide-active {
		opacity: 1;
	  }
  
	  // üîΩ „Åì„Åì„ÅåÈáçË¶ÅÔºà„Éà„Éº„É≥„ÉÄ„Ç¶„É≥„ÅÆ„Ç™„Éº„Éê„Éº„É¨„Ç§Ôºâ
	  &::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.4); // ‚Üê ÈÄèÊòéÂ∫¶0.4„Åß‚Äú„ÇÑ„ÇÑÊöó„Åè‚Äù
		z-index: 1;
	  }
	}
  }
  
  /* Swiper„ÅÆËÉåÊôØÁîªÂÉèË™øÊï¥ */
  .swiper-slide {
	background-size: cover;
	background-position: center top;
	height: 100vh;
  }
  
  /* „Éö„Éº„Ç∏„Éç„Éº„Ç∑„Éß„É≥„ÅÆ„Éá„Ç∂„Ç§„É≥ */
  .swiper-pagination-bullet {
	background-color: rgba(255, 255, 255, 0.6);
	width: 12px;
	height: 12px;
  }
  
  .swiper-pagination-bullet-active {
	background-color: rgba(255, 255, 255, 1);
  }
  