@use "variables" as v;
@use "mixin" as m;

#header {
  transition: transform 0.4s ease;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 155px;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.3);
  pointer-events: auto;
  &.header--hidden {
    transform: translateY(-100%);
  }
  //   .site-title,
  //   .contact_btn {
  //     z-index: 60;
  //   }
  .swiper-slide {
    filter: brightness(0.7) blur(1px); /* Êòé„Çã„ÅïË™øÊï¥„Å®ËªΩ„ÅÑ„Åº„Åã„Åó */
    // opacity: 0.8;
  }
  .site-title {
    display: flex;
    align-items: center;
    position: absolute;
    top: 30px;
    left: 30px;
    text-align: left;
    z-index: 20;
    &::before {
      content: "";
      position: absolute;
      top: -20px; /* ‰∏ä„Å´Â∞ë„Åó‰ΩôÁôΩ */
      left: -20px;
      width: calc(100% + 40px);
      height: calc(100% + 40px);
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.3));
      border-radius: 8px;
      z-index: -1;
      pointer-events: none;
    }
    img {
      width: 200px;
      display: block;
      margin: 0 auto;
      margin-bottom: 10px;
    }
    p {
      max-width: 1000px;
      padding-left: 20px;
      text-align: center;
      color: #ffffff;
      font-size: 0.8rem;
      font-weight: bold;
      line-height: 1.5;
      /* ‚úÖ „ÉÜ„Ç≠„Çπ„Éà„ÅÆË¶ñË™çÊÄß„Ç¢„ÉÉ„Éó */
      text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
    }
    nav {
      position: absolute;
      top: 50px;
      right: 100px;
      .navi {
        display: flex;
        gap: 20px;
        margin: 0;
        padding: 0;
        li {
          display: flex;
          align-items: center;
        }
        a {
          position: relative;
          color: white;
          font-size: 32px;
          // text-shadow: 1px 2px #969696;
          transition: color 0.3s;

          &::after {
            content: "";
            position: absolute;
            left: 0;
            bottom: -3px; // ‰∏ãÁ∑ö„ÅÆ‰ΩçÁΩÆË™øÊï¥
            width: 0;
            height: 2px;
            background-color: v.$white; // ‰∏ãÁ∑ö„ÅÆËâ≤
            transition: width 0.3s ease;
          }

          &:hover {
            color: #cccccc7d; // Êöó„Åè„Åô„ÇãËâ≤„ÇíË™øÊï¥
            text-shadow: none; // „Ç∑„É£„Éâ„Ç¶„ÇíÊ∂à„Åô
          }

          &:hover::after {
            width: 100%;
          }
        }
      }
    }
  }
  .contact_btn {
    pointer-events: auto; // ‚Üê „Åì„ÅìÈáçË¶ÅÔºÅ
    z-index: 9999; // ‚Üê z-index„ÅØÊúÄ‰∏ä‰Ωç„Å´ÊåÅ„Å£„Å¶„Åè„Çã
    position: absolute;
    top: 50%;
    right: 30px;
    transform: translateY(-50%);
    // z-index: 30;
    &::before {
      content: "";
      position: absolute;
      top: -20px; /* ‰∏ä„Å´Â∞ë„Åó‰ΩôÁôΩ */
      left: -20px;
      width: calc(100% + 40px);
      height: calc(100% + 40px);
      background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.3));
      border-radius: 8px;
      z-index: -1;
      pointer-events: none;
    }

    a {
      color: #ffffff;
      font-size: 1.5rem;
      z-index: 100;
      pointer-events: auto;
    }
  }
}

.overlay {
  pointer-events: none;
  position: relative;
}

.overlay .picture a {
  pointer-events: auto;
}

.contact_btn a {
  pointer-events: auto;
  z-index: 9999;
}
